.card_holder .card_wrapper:nth-of-type(2):hover{
    transform: translateY(-10vh) rotateX(25deg) rotateZ(-20deg);
} */



.card_topper {
    background: #1e1e1e;;
    display: flex;
}

.card_button {
    border-radius: 50%;
    height: 10px;
    width: 10px;
    margin: 5px;
    z-index: 3;
}

.card_button:nth-of-type(1) {
    background: rgb(219, 20, 60) !important;
}

.card_button:nth-of-type(2) {
    background: goldenrod;
}

.card_button:nth-of-type(3) {
    background: chartreuse;
}

.card {
    margin: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    object-fit: cover;
    overflow: hidden;
    background: #252525;
}

.card_wrapper:hover {
    box-shadow: -0px 1px 0px 1px #0b5eac22, -0px 5px 2px 0px rgba(0, 222, 255, 0.1);
    cursor: pointer;
    filter: grayscale(0);
}
